  background-color: transparent;
}
.page-header--inner .page-header__wrapper {
  border-bottom: 1px solid #000000;
}
.page-header--inner .page-navigation__item::before, .page-header--inner .page-navigation__item::after {
  content: "";
  position: absolute;
  bottom: -1px;
