}
@media (min-width: 640px) {
  .page-header--inner .page-navigation__toggle::after {
    top: 42px;
    width: 36px;
    height: 4px;
  }
}
.page-header--inner .page-navigation__toggle--open::before, .page-header--inner .page-navigation__toggle--open::after {
  content: "";
  position: absolute;
  top: 34px;
  right: 4px;
  width: 30px;
  height: 3px;
  background-color: #000000;
  box-shadow: none;
  transition: transform 0.5s ease-in-out, box-shadow 0.7s ease-in-out;
}
@media (min-width: 640px) {
  .page-header--inner .page-navigation__toggle--open::before, .page-header--inner .page-navigation__toggle--open::after {
    top: 34px;
    right: 8px;
