.about__wrapper {
  max-width: 1160px;
  padding: 30px 10px 0;
  margin: 0 auto;
  margin-bottom: 79px;

  @media (min-width: $tablet-width) {
    padding: 45px 10px 0;
  }

  @media (min-width: $desktop-width-sx) {
    margin-bottom: 30px;
    padding: 92px 103px;
  }
}

.about__title {
  @include title-font-mobile;
  margin: 0;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    margin-bottom: 31px;
    font-size: 35px;
    line-height: 52px;
  }

  @media (min-width: $desktop-width-sx) {
    margin-bottom: 60px;
    font-size: 56px;
    line-height: 83px;
  }
}

.about__list {
  list-style: none;
  counter-reset: section;
  margin: 0;
  padding: 0;
}

.about__item {
  min-height: 434px;
  margin-bottom: 34px;
  position: relative;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    margin-bottom: 38px;
  }

  @media (min-width: $desktop-width-sx) {
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    min-height: 589px;
    margin-bottom: 140px;
  }

  &:nth-child(odd) {
    @media (min-width: $tablet-width) {
      padding-right: 318px;
    }

    @media (min-width: $desktop-width) {
      padding-right: 600px;
    }
  }

  &:nth-child(even) {
    @media (min-width: $tablet-width) {
      padding-left: 318px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 600px;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.about__item::before {
  counter-increment: section;
  content: counter(section) "";
  display: inline-block;
  height: 24px;
  width: 24px;
  margin-bottom: 12px;
  padding: 2px;
  box-sizing: border-box;
  background-color: $basic-violet;
  border-radius: 7px;
  color: $basic-white;
  text-align: center;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;

  @media (min-width: $tablet-width) {
  height: 38px;
  width: 38px;
  margin-bottom: 17px;
  padding: 5px;
  font-size: 22px;
  line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    height: 59px;
    width: 59px;
    margin-bottom: 36px;
    padding: 16px;
  }
}

.about__item__title {
  margin: 0;
  margin-bottom: 19px;

  @media (min-width: $tablet-width) {
    font-size: 25px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width-sx) {
    max-width: 534px;
    font-size: 35px;
    line-height: 41px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 25px;
    font-size: 45px;
    line-height: 53px;
  }
}

.about__item__img {
  display: block;
  width: 300px;
  height: 182px;
  margin-bottom: 6px;
  border-radius: 10px;
  object-fit: cover;

  @media (min-width: $tablet-width) {
    position: absolute;
    margin-bottom: 0;
    width: 295px;
    height: 430px;
    top: 0;
  }

  @media (min-width: $desktop-width) {
    width: 500px;
    height: 589px;
  }
}

.about__item:nth-child(odd)>.about__item__img {
  @media (min-width: $tablet-width) {
    right: 0;
  }
}

.about__item:nth-child(even)>.about__item__img {
  @media (min-width: $tablet-width) {
    left: 0;
  }
}

.about__item__text {
  margin-bottom: 17px;

  @media (min-width: $desktop-width-sx) {
    max-width: 534px;
    font-size: 19px;
    line-height: 26px;
  }

  @media (min-width: $desktop-width) {
    max-width: 560px;
    font-size: 22px;
    line-height: 30px;
  }
}

.about__item__text p {
  margin: 0 0 25px;
}

.about__item__text p:last-child {
  margin: 0;
}

.about__item__link {
  @include button-settings-mobile;
  display: inline-block;
  transition: all 0.3s;

  @media (min-width: $tablet-width) {
    margin-top: auto;
  }

  @media (min-width: $desktop-width-sx) {
    width: 230px;
    padding: 12px;
    font-size: 19px;
    line-height: 22px;
  }

  @media (min-width: $desktop-width) {
    min-height: 60px;
    padding: 18px;
    font-size: 16px;
    line-height: 22px;
  }

  &:hover {
    color: $basic-white;
    background-color: $light-blue-button;
    transition: all 0.3s;
  }
}
